/* Item schedule */		
.uap .item-async {
    position: absolute;

    user-select: none;
    display: flex;
    flex-direction: column;
    align-items: center;
    top: -8px;
    right: 30px;

}

    .uap .item-async__btn {
        font-size: 13px;
        font-weight: 500;
        padding: 2px 8px;

        color: #818181;

        border: 1px solid #cdcdcd;
        border-radius: 8px;

        cursor: pointer;
        white-space: nowrap;

        display: inline-block;

        opacity: 0;

        transition: all 150ms ease-in-out;
    }

        .uap .item--async .item-async__btn {
            background-color: #F2F9FE;
        }

        .uap .item-async__btn strong {
            
            text-transform: lowercase;
            color: black;
        }

        .uap .item:hover .item-async__btn,
        .uap .item--open .item-async__btn,
        .uap .item--async .item-async__btn  {
            opacity: 1;
        }

        .uap .item-async__btn .uo-icon {
            margin-right: 6px;
            color: #0790e8;
        }
    
    .uap .async-button__badge {
        z-index: 999;

        position: absolute;
        top: -10px;
        right: 0;
        padding: 1px 6px;
        border-radius: 8px;

        background-color: #FC1A57;
        color: white;
        font-size: 10px;
        font-weight: bold;

    }    
    
        .uap .item--async .async-button__badge {
            display: none;
        }
        
    .uap .async-remove {
        z-index: 999;
        display: none;
        
        margin-top: 4px;
        font-size: 13px;
        font-weight: 500;
        padding: 2px 8px;

        background-color: #FFF;
        color: #E85113;

        border: 1px solid #cdcdcd;
        border-radius: 6px;

        vertical-align: middle;

        cursor: pointer;
        white-space: nowrap;

        transition: all 150ms ease-in-out;
    }

        .uap .item-async:hover .async-remove {
            display: block;
        }

        .uap .async-remove:before {
            content: "";
            width: 0;
            height: 0;
            position: absolute;
            z-index: 2;
            top: 24px;
            margin-left: auto;
            margin-right: auto;
            left: 0;
            right: 0;
            text-align: center;
            border-left: 5px solid transparent;
            border-right: 5px solid transparent;
            border-bottom: 5px solid #fff;
        }

        .uap .async-remove:after {
            z-index: 1;
            top: 23px;
            margin-left: auto;
            margin-right: auto;
            left: 0;
            right: 0;
            text-align: center;
            border-left: 6px solid transparent;
            border-right: 6px solid transparent;
            border-bottom: 5px solid #c7c7c7;
            content: "";
            width: 0;
            height: 0;
            position: absolute;
        }

        .uap .async-remove .uo-icon {
            margin-right: 4px;
            vertical-align: middle;
        }

// Modal
.uap .modal--async-action .modal__box {
    width: 480px;
    overflow-y: visible;
}

    .uap .modal--async-action .modal__content {
        min-height: 250px;
        max-height: none;
    }

        .uap .async-action-modal label {
            font-weight: bold;
            margin-bottom: 0 !important;
        }	

        .uap .delay-options, .uap .schedule-options {
            padding-left: 23px;
        }

        .uap .async-mode__description {
            color: #818181;
            display: inline-block;
            margin-bottom: 10px;
        }

        .uap .delay-fields {
            margin-bottom: 32px;
            position: relative;
        }

            .uap .delay-fields__description {
                font-weight: 500;
            }

        .uap .delay-fields .form-element__error {
            position: absolute;
            bottom: -32px;
        }

        .uap .delay-fields > div, .uap .schedule-fields > div {
            display: inline-block;
            vertical-align: middle;
            margin-bottom: 0 !important;
        }

        .uap .form-element .delay-fields__number {
            width: 50px;
        }

        .uap .form-element .delay-fields__number input {
            border-top-right-radius: 0;
            border-bottom-right-radius: 0;
            height: 37px;
        }

        .uap .form-element .delay-fields__unit {
            width: 110px;
            margin-right: 10px;
        }

        .uap .form-element .delay-fields__unit .select2-selection--single {
            border-top-left-radius: 0;
            border-bottom-left-radius: 0;
            min-height: 37px;
            line-height: 14px !important;
            padding-top: 0px;
            padding-bottom: 0px;
            margin-bottom : 0 !important;
        }

        .uap .form-element .delay-fields__unit .select2-selection--single {
            border-top-left-radius: 0;
            border-bottom-left-radius: 0;
            min-height: 37px;
            line-height: 14px !important;
            padding-top: 0px;
            padding-bottom: 0px;
            margin-bottom : 0 !important;
        }

        .uap .form-element .schedule-fields__date {
            width: auto;
            min-width: 140px;
            margin-right: 15px;
        }

        .uap .form-element .schedule-fields__time {
            width: auto;
            min-width: 100px;
        }

        .uap .schedule-fields__description {
            display: inline-block;
            margin-top: 6px;
            font-size: 80%;
        }

            .uap .schedule-fields__description .uo-icon {
                font-size: 10px;
            }

        .uap .schedule-notice {
            border-radius: 8px;
            padding: 4px 8px;
            margin-top: 10px;
            font-weight: 500;
        }

            .uap .schedule-notice .uo-icon {
                margin-left: 4px;
                font-size: 12px;
            }

        .uap .schedule-notice__yellow {
           background-color: #FFF3CF;
        }

        .uap .schedule-fields__description .uo-icon {
            margin-left: 4px;
			}

    .uap .async-notice__blue {
        padding: 4px 10px;
        border-radius: 8px;
        background-color: #E2EDF9;
        font-weight: 500;
    }
        .uap .async-notice__blue .uo-icon {
            margin-right: 4px;
            font-size: 12px;
        }

// Hide the search field in the dropdown            
