.uap-placeholder {
	.item {
		&-icon {
			box-shadow: none;
			border-color: rgba( $placeholder-gray, .6 );

			&:before {
				content: '';

				width: 20px;
				height: 20px;

				background: $placeholder-gray;
				border-radius: 50%;
			}
		}

		&-actions__btn {
			color: $placeholder-gray;
		}

		&-title {
			&:before {
				content: '';

				width: 200px;
				height: 1em;

				display: inline-block;

				background: $placeholder-gray;
				border-radius: $placeholder-border-radius;
			}
		}

		&--action {
			.item-title {
				&:before {
					width: 60px;
					height: 1.3em;
					border-radius: $placeholder-border-radius;

					margin-top: 8px;
				}
			}
		}

		&-integration {
			box-shadow: none;
			border-color: rgba( $placeholder-gray, .6 );

			&__logo {
				&:before {
					content: '';

					width: 80%;
					height: 40px;

					display: block;

					position: absolute;
					top: 50%;
					left: 50%;
					transform: translate(-50%,-50%);

					background: $placeholder-gray;
					border-radius: $placeholder-border-radius;
				}
			}

			&__name {
				position: relative;

				&:before {
					content: '';

					width: 80%;
					height: 1em;

					background: $placeholder-gray;
					border-radius: $placeholder-border-radius;

					display: block;

					margin-left: 10%;
				}
			}
		}
	}

	&-checkbox {
		display: flex;

		&__field {
			width: 1em;
			height: 1em;
		}

		&__label {
			width: 150px;
			height: 1em;
			margin-left: 10px;
		}

		&__field,
		&__label {
			background: $placeholder-gray;
			border-radius: $placeholder-border-radius;
		}
	}

	.metabox__footer {
		border-color: rgba( $placeholder-gray, .6 );
	}

	.publish-row {
		$relative-class: '.publish-row';

		&__icon,
		&__name,
		&__value,
		&__edit {
			height: 1em;

			font-size: 15px;

			display: inline-block;

			background: $placeholder-gray;
			border-radius: $placeholder-border-radius;
		}

		&__name,
		&__value {
			width: 60px;
		}

		&:nth-child(2),
		&:nth-child(2) {
			#{ $relative-class }__name,
			#{ $relative-class }__value {
				width: 70px;
			}
		}

		&:nth-child(3),
		&:nth-child(3) {
			#{ $relative-class }__name,
			#{ $relative-class }__value {
				width: 55px;
			}
		}

		&__edit {
			width: 30px;

			background: $placeholder-blue;
		}
	}

	.publish-footer {
		font-size: 15px;
		line-height: 1;

		&__move-to-draft {
			width: 70px;
			height: 1.2em;

			display: inline-block;

			background: $placeholder-red;
			border-radius: $placeholder-border-radius;
		}

		&__right {
			width: 40px;
			height: 1.2em;

			display: inline-block;

			background: $placeholder-gray;
			border-radius: $placeholder-border-radius;
		}
	}
}