@mixin icons-font {
	font-family: 'Uncanny Automator Icons' !important;
	speak: none;
	font-style: normal;
	font-weight: normal;
	font-variant: normal;
	text-transform: none;
	line-height: 1;

	/* Better Font Rendering */
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

@keyframes placeHolderShimmer {
	0% {
		opacity: 1;
	}
	100% {
		opacity: .5;
	}
}