.uap {
	&-token {
		display: inline-flex;
		align-items: center;

		background: #eee;

		line-height: 1;

		padding: 3px 8px;

		border-radius: 150px;
 
        box-shadow: 0 0 0 2px transparent;
        transition: box-shadow 150ms ease-in-out;

        cursor: pointer;

		img {
			width: 12px;
			height: 11px;

			margin-right: 5px;

			object-fit: contain;
			object-position: center;

			display: block;
		}

		.uo-icon {
			width: 14px;
			height: 14px;

			border: 1px solid #444;
			border-radius: 50%;

			font-size: 9px;
			color: #444;
			text-align: center;
			line-height: 15px;
			background: #fff;

			opacity: .8;

			margin-right: 5px;
		}

		&__name {
			color: #444444;

			font-size: 14px;
			font-weight: 600;
		}

		&--invalid {
			background: #f9e2e2;

			.uo-icon {
				color: #c00;
				border-color: #c00;
			}
		}

		&--cm-focused {
			box-shadow: 0 0 0 2px #0790e8;
		}

		:hover {}

		/* Exceptions for the tokens inside the token selector */

		.form-variable-group-source__description & {
			padding: 2px 6px;

			&__name {
				font-size: 12px;
			}
		}
	}
}