}

.uap .uap-btn--transparent.uap-btn--loading,
.uap .uap-btn--transparent.uap-btn--disabled,
.uap .uap-btn--transparent:disabled,
.uap .uap-btn--transparent:hover,
.uap .uap-btn--transparent:focus {
