}

.uap .uap-btn--loading:before {
  content: "";
  font-family: "Uncanny Automator Icons";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #fff;
  font-size: 16px;
  animation: rotating-centered 2s linear infinite;
