}

.uap .item:nth-child(2) {
  z-index: 18;
}

.uap .item:nth-child(3) {
  z-index: 17;
}

.uap .item:nth-child(4) {
  z-index: 16;
}

.uap .item:nth-child(5) {
  z-index: 15;
}

.uap .item:nth-child(6) {
  z-index: 14;
}

.uap .item:nth-child(7) {
  z-index: 13;
}

.uap .item:nth-child(8) {
  z-index: 12;
}

.uap .item:nth-child(9) {
  z-index: 11;
}

.uap .item:nth-child(10) {
  z-index: 10;
}

.uap .item:nth-child(11) {
  z-index: 9;
}

.uap .item:nth-child(12) {
  z-index: 8;
}

.uap .item:nth-child(13) {
  z-index: 7;
}

.uap .item:nth-child(14) {
  z-index: 6;
}

.uap .item:nth-child(15) {
  z-index: 5;
}

.uap .item:nth-child(16) {
  z-index: 4;
}

.uap .item:nth-child(17) {
  z-index: 3;
}

.uap .item:nth-child(18) {
  z-index: 2;
}

.uap .item:nth-child(19) {
  z-index: 1;
}

.uap .item:nth-child(20) {
  z-index: 0;
}

.uap .item:after {
  content: "";
  display: block;
  clear: both;
}

.uap .item ~ .item {
  margin-top: 30px;
}

.uap .item ~ .item:before {
  content: "";
  width: 100%;
  height: 1px;
  background: #e9e9e9;
  position: absolute;
  left: 0;
  top: -15px;
}

.uap .item--error {
  /* VALIDATION ERROR */
}

.uap .item--margintop {
  margin-top: 15px;
}

.uap .item__content:not(:empty) {
  width: 100%;
  float: left;
  margin-top: 10px;
}

.uap .item-content__description {
  font-size: 15px;
  color: #747474;
  margin-top: 3px;
}

.uap .item-content__description .uo-icon {
  font-size: 12px;
}

/* Item actions */
.uap .item-actions {
  position: absolute;
  user-select: none;
  top: -15px;
  right: -8px;
  z-index: 10;
}

.uap .item-actions__btn {
  color: #b2b2b2;
  font-size: 18px;
  line-height: 1;
  padding: 10px;
  /* Just to make it easier to click */
  cursor: pointer;
  transition: color 150ms ease-in-out;
}

.uap .item-actions--open .item-actions__btn,
.uap .item-actions__btn:hover {
  color: #6b6b6b;
}

.uap .item-actions__dropdown {
  position: absolute;
  bottom: 0;
  right: 2px;
  display: none;
}

.uap .item-actions--open .item-actions__dropdown {
  display: block;
}

.uap .item-actions-dropdown__ul {
  position: absolute;
  top: 0;
  right: 0;
  display: block;
  margin: 0;
  /* Reset browser margin */
  padding: 0;
  /* Reset browser padding */
  list-style: none;
  background: #fff;
  border: 1px solid #c7c7c7;
  border-radius: 8px;
  box-shadow: 0 5px 15px 0 rgba(0, 0, 0, 0.2);
}

.uap .item-actions-dropdown__ul:before,
.uap .item-actions-dropdown__ul:after {
  content: "";
  width: 0;
  height: 0;
  position: absolute;
}

.uap .item-actions-dropdown__ul:before {
  z-index: 2;
  top: -8px;
  right: 9px;
  border-left: 8px solid transparent;
  border-right: 8px solid transparent;
  border-bottom: 8px solid #fff;
}

.uap .item-actions-dropdown__ul:after {
  z-index: 1;
  top: -9px;
  right: 7px;
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
  border-bottom: 9px solid #c7c7c7;
}

.uap .item-actions-dropdown__action {
  padding: 0;
  /* Reset WP padding */
  margin: 0;
  /* Reset WP margin */
  position: relative;
}

.uap .item-actions-dropdown__action:not(:last-child) {
  border-bottom: 1px solid #c9c9c9;
}

.uap .item-actions-dropdown__action a,
.uap .item-actions-dropdown__action span {
  color: #3c3c3c;
  max-width: 100%;
  min-width: 100px;
  cursor: pointer;
  display: block;
  padding: 7px 10px;
  transition: color 150ms ease-in-out;
}

.uap .item-actions-dropdown__action a:hover,
.uap .item-actions-dropdown__action span:hover {
  color: #0790e8;
}

.uap .item-actions-dropdown__action:after {
  content: "";
  opacity: 0;
  transition: opacity 150ms ease-in-out;
}

/* Loading */
.uap .item-actions-dropdown__action--loading {
  pointer-events: none;
  cursor: default;
}

.uap .item-actions-dropdown__action--loading a,
.uap .item-actions-dropdown__action--loading span {
  color: #8e8e8e;
}

.uap .item-actions-dropdown__action--loading:after {
  opacity: 1;
  content: "";
  font-family: "Uncanny Automator Icons";
  position: absolute;
  top: 50%;
  right: 3px;
  z-index: 1;
  color: #8e8e8e;
  font-size: 15px;
  animation: rotating-centered 2s linear infinite;
}

/* Item icon */
.uap .item-icon {
  pointer-events: none;
  /* Disable user interactions */
  width: 45px;
  height: 45px;
  background: #fff;
  border-radius: 50%;
  border: 1px solid #cdcdcd;
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
  transition: all 150ms ease-in-out;
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.1);
  user-select: none;
}

.uap .item-icon:not(:empty) {
  cursor: pointer;
}

.uap .item-icon:not(:empty):hover {
  border-color: #0790e8;
}

.uap .item-icon:not(:empty):active {
  box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0.5);
}

.uap .item-icon:before {
  content: "";
  /* uo-icon--bolt */
  color: #c4c4c4;
  font-family: "Uncanny Automator Icons";
  font-size: 20px;
  opacity: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: opacity 150ms ease-in-out;
}

.uap .item--missing .item-icon {
  border-style: dashed;
  border-color: #bfbfbf;
}

.uap .item--missing .item-icon:before {
  content: "";
}

.uap .item-icon:empty:before {
  opacity: 1;
}

.uap .item-icon img {
  width: 24px;
  height: 24px;
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.uap .item-icon:after {
  /* Mask */
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

/* Item title */
.uap .item-title {
  width: 100%;
  user-select: none;
}

.uap .item-title__integration {
  width: 100%;
  margin-bottom: 7px;
}

.uap .item-title__tag {
  pointer-events: none;
  /* Disable user interactions */
  font-size: 13px;
  font-weight: 500;
  padding: 2px 12px;
  color: #818181;
  border: 1px solid #cdcdcd;
  border-radius: 150px;
  /* Big value */
  cursor: pointer;
  white-space: nowrap;
  display: inline-block;
  transition: all 150ms ease-in-out;
}

.uap .item-title__tag:hover {
  color: #fff;
  background: #818181;
  border-color: #818181;
}

.uap .item-title__describe-container {
  width: 100%;
  display: flex;
  margin-bottom: -10px;
}

.uap .item-title__describe {
  flex: 1 1 auto;
  color: #343434;
  font-size: 16px;
  margin-bottom: 5px;
}

.uap .item--missing .item-title__describe {
  opacity: 0.6;
}

.uap .item-title__normal,
.uap .item-title__token {
  display: inline-block;
  vertical-align: middle;
  margin-bottom: 5px;
  margin-right: 8px;
  transition: all 150ms ease-in-out;
}

.uap .item:not(.item--closure) .item-title__normal {
  /*
  border-bottom: 1px dashed #999999;
  padding-bottom: 2px;
  cursor: pointer;
  */
}

.uap .item:not(.item--closure) .item-title__normal:hover {
  /*
  color: #0790e8;
  border-color: #0790e8;
  */
}

.uap .item-title__token {
  border: 1px solid #0790e8;
  color: #0790e8;
  font-size: 14px;
  padding: 3px 8px;
  border-radius: 8px;
  font-weight: 500;
  cursor: pointer;
  /* white-space: nowrap; */
  display: inline-block;
  vertical-align: middle;
  transition: background 150ms ease-in-out, color 150ms ease-in-out, border 150ms ease-in-out, box-shadow 150ms ease-in-out;
}

.uap .item-title__token[uap-tooltip]::after {
  line-height: 1.4;
  white-space: normal;
  overflow: visible;
  text-overflow: clip;
}

.uap .item-title__token:hover {
  border-color: #0373ba;
}

.uap .item-title__token--filled {
  background: #0790e8;
  color: #fff;
}

.uap .item-title__token--filled:hover {
  background: #0373ba;
}

.uap .item-title__token.item-title__token--open {
  border-color: #0373ba;
}

.uap .item-title__token--filled.item-title__token--open {
  background: #0373ba;
}

.uap .item-title__token--open:not(.item-title__token--filled) {
  color: #0373ba;
}

.uap .item-title__token--error {
  /* VALIDATION ERROR */
  color: #fff !important;
  background: #e85113 !important;
  border-color: #e85113 !important;
}

.uap .item-title__token--error-shake {
  animation: shake__variable 0.82s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;
}

.uap .item-title__token--error:hover {
  background: #be4a46 !important;
  border-color: #be4a46 !important;
}

@keyframes shake__variable {
  10%, 90% {
    transform: translate3d(-1px, 0, 0);
  }
  20%, 80% {
    transform: translate3d(2px, 0, 0);
  }
  30%, 50%, 70% {
    transform: translate3d(-4px, 0, 0);
  }
  40%, 60% {
    transform: translate3d(4px, 0, 0);
  }
}
/* Missing item */
.uap .item--missing .item-title__token,
.uap .item--missing .item-title__token:hover {
  color: #343434;
  border-color: #a2a2a2;
  cursor: default;
}

.uap .item--missing .item-title__token--filled,
.uap .item--missing .item-title__token--filled:hover {
  color: #fff;
  border-color: #929292;
  background: #929292;
