/* Normal checkbox [] */
.uap .form-checkbox:not(.form-checkbox--toggle) .checkbox--show {
  box-sizing: border-box;
  position: absolute;
  top: 2px;
  left: 0;
  height: 16px;
  width: 16px;
  border: 1px solid #afafaf;
  border-radius: 4px;
  transition: all 150ms ease-in-out;
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.1);
