  height: 100%;
  border-radius: 150px;
  border: 2px solid #c8c8c8;
  display: block;
  position: absolute;
  box-sizing: border-box;
  z-index: 1;
  transition: border 150ms ease-in-out, background 150ms ease-in-out;
}

