  border-color: #6c7781;
}

.uap .form-checkbox.form-checkbox--toggle .checkbox--show:after {
  content: "";
  width: 8px;
  height: 8px;
  background: #afafaf;
  border-radius: 50%;
  display: block;
  position: absolute;
  top: 50%;
  left: 5px;
  transform: translateY(-50%);
  z-index: 2;
  transition: left 150ms ease-in-out, background 150ms ease-in-out;
}

