#recipe_categorydiv .selectit {
  width: auto;
  position: relative;
  display: block;
  padding-top: 2px;
  padding-bottom: 2px;
  padding-left: 23px;
  padding-right: 25px;
  transition: opacity 150ms ease-in-out;
  line-height: 1.4;
}
#recipe_categorydiv .selectit input {
  position: absolute;
  top: 7px;
  left: 0px;
}
#recipe_categorydiv .selectit input:disabled {
  border: 1px solid #7e8993;
}
#recipe_categorydiv .selectit:before {
  content: "";
  font-family: "Uncanny Automator Icons";
  opacity: 0;
  position: absolute;
  top: 10px;
  right: -6px;
  z-index: 1;
  color: #8e8e8e;
  font-size: 15px;
  animation: rotating-centered 2s linear infinite;
  transition: opacity 150ms ease-in-out;
}
#recipe_categorydiv .selectit:after {
  font-family: "Uncanny Automator Icons";
  opacity: 0;
  position: absolute;
  top: 10px;
  right: -6px;
  z-index: 1;
  transform: translate(-50%, -50%);
  font-size: 15px;
  transition: opacity 150ms ease-in-out;
}
