  pointer-events: none;
  cursor: default;
  opacity: 0.8;
}
#recipe_categorydiv .uap-taxonomy-loading:before {
  opacity: 1;
}
#recipe_categorydiv .uap-taxonomy-success:after {
  opacity: 1;
  content: "";
  color: #6bc45a;
}
#recipe_categorydiv .uap-taxonomy-error:after {
  opacity: 1;
  content: "";
  color: #e85113;
}

