/** Inherit styles */

.select2-container--uap-select2 {
	z-index: 9999999
}

.select2-container--uap-select2 .select2-selection--single {
	background-color: #fff;
	border: 1px solid #aaa;
	border-radius: 8px
}

.select2-container--uap-select2 .select2-selection--single .select2-selection__rendered {
	color: #444;
	line-height: 32px;
}

.select2-container--uap-select2 .select2-selection--single .select2-selection__clear {
	cursor: pointer;
	float: right;
	font-weight: bold
}

.select2-container--uap-select2 .select2-selection--single .select2-selection__placeholder {
	color: #999
}

.select2-container--uap-select2 .select2-selection--single .select2-selection__arrow {
	height: 26px;
	position: absolute;
	top: 1px;
	right: 1px;
	width: 20px
}

.select2-container--uap-select2 .select2-selection--single .select2-selection__arrow b {
	border-color: #888 transparent transparent transparent;
	border-style: solid;
	border-width: 5px 4px 0 4px;
	height: 0;
	left: 50%;
	margin-left: -4px;
	margin-top: -2px;
	position: absolute;
	top: 50%;
	width: 0
}

.select2-container--uap-select2[dir="rtl"] .select2-selection--single .select2-selection__clear {
	float: left
}

.select2-container--uap-select2[dir="rtl"] .select2-selection--single .select2-selection__arrow {
	left: 1px;
	right: auto
}

.select2-container--uap-select2.select2-container--disabled .select2-selection--single {
	background-color: #eee;
	cursor: default
}

.select2-container--uap-select2.select2-container--disabled .select2-selection--single .select2-selection__clear {
	display: none
}

.select2-container--uap-select2.select2-container--open .select2-selection--single .select2-selection__arrow b {
	border-color: transparent transparent #888 transparent;
	border-width: 0 4px 5px 4px
}

.select2-container--uap-select2 .select2-selection--multiple {
	background-color: white;
	border: 1px solid #aaa;
	border-radius: 8px;
	cursor: text
}

.select2-container--uap-select2 .select2-selection--multiple .select2-selection__rendered {
	box-sizing: border-box;
	list-style: none;
	margin: 0;
	padding: 0 5px;
	width: 100%
}

.select2-container--uap-select2 .select2-selection--multiple .select2-selection__rendered li {
	list-style: none
}

.select2-container--uap-select2 .select2-selection--multiple .select2-selection__clear {
	cursor: pointer;
	float: right;
	font-weight: bold;
	margin-top: 5px;
	margin-right: 10px;
	padding: 1px
}

.select2-container--uap-select2 .select2-selection--multiple .select2-selection__choice {
	background-color: #e4e4e4;
	border: 1px solid #aaa;
	border-radius: 8px;
	cursor: default;
	float: left;
	margin-right: 5px;
	margin-top: 5px;
	padding: 0 5px
}

.select2-container--uap-select2 .select2-selection--multiple .select2-selection__choice__remove {
	color: #999;
	cursor: pointer;
	display: inline-block;
	font-weight: bold;
	margin-right: 2px
}

.select2-container--uap-select2 .select2-selection--multiple .select2-selection__choice__remove:hover {
	color: #333
}

.select2-container--uap-select2[dir="rtl"] .select2-selection--multiple .select2-selection__choice,
.select2-container--uap-select2[dir="rtl"] .select2-selection--multiple .select2-search--inline {
	float: right
}

.select2-container--uap-select2[dir="rtl"] .select2-selection--multiple .select2-selection__choice {
	margin-left: 5px;
	margin-right: auto
}

.select2-container--uap-select2[dir="rtl"] .select2-selection--multiple .select2-selection__choice__remove {
	margin-left: 2px;
	margin-right: auto
}

.select2-container--uap-select2.select2-container--focus .select2-selection--multiple {
	border: solid black 1px;
	outline: 0
}

.select2-container--uap-select2.select2-container--disabled .select2-selection--multiple {
	background-color: #eee;
	cursor: default
}

.select2-container--uap-select2.select2-container--disabled .select2-selection__choice__remove {
	display: none
}

.select2-container--uap-select2.select2-container--open.select2-container--above .select2-selection--single,
.select2-container--uap-select2.select2-container--open.select2-container--above .select2-selection--multiple {
	border-top-left-radius: 0;
	border-top-right-radius: 0
}

.select2-container--uap-select2.select2-container--open.select2-container--below .select2-selection--single,
.select2-container--uap-select2.select2-container--open.select2-container--below .select2-selection--multiple {
	border-bottom-left-radius: 0;
	border-bottom-right-radius: 0
}

.select2-container--uap-select2 .select2-search--dropdown .select2-search__field {
	border: 1px solid #aaa
}

.select2-container--uap-select2 .select2-search--inline .select2-search__field {
	background: transparent;
	border: none;
	outline: 0;
	box-shadow: none;
	-webkit-appearance: textfield
}

.select2-container--uap-select2 .select2-results>.select2-results__options {
	max-height: 200px;
	overflow-y: auto
}

.select2-container--uap-select2 .select2-results__option[role=group] {
	padding: 0
}

.select2-container--uap-select2 .select2-results__option[aria-disabled=true] {
	color: #999
}

.select2-container--uap-select2 .select2-results__option[aria-selected=true] {
	background-color: #ddd
}

.select2-container--uap-select2 .select2-results__option .select2-results__option {
	padding-left: 1em
}

.select2-container--uap-select2 .select2-results__option .select2-results__option .select2-results__group {
	padding-left: 0
}

.select2-container--uap-select2 .select2-results__option .select2-results__option .select2-results__option {
	margin-left: -1em;
	padding-left: 2em
}

.select2-container--uap-select2 .select2-results__option .select2-results__option .select2-results__option .select2-results__option {
	margin-left: -2em;
	padding-left: 3em
}

.select2-container--uap-select2 .select2-results__option .select2-results__option .select2-results__option .select2-results__option .select2-results__option {
	margin-left: -3em;
	padding-left: 4em
}

.select2-container--uap-select2 .select2-results__option .select2-results__option .select2-results__option .select2-results__option .select2-results__option .select2-results__option {
	margin-left: -4em;
	padding-left: 5em
}

.select2-container--uap-select2 .select2-results__option .select2-results__option .select2-results__option .select2-results__option .select2-results__option .select2-results__option .select2-results__option {
	margin-left: -5em;
	padding-left: 6em
}

.select2-container--uap-select2 .select2-results__option--highlighted[aria-selected] {
	background-color: #5897fb;
	color: white
}

.select2-container--uap-select2 .select2-results__group {
	cursor: default;
	display: block;
	padding: 6px
}

/** Custom styles */

.uap .select2-container {
		width: 100% !important;
	}

	.uap .select2-selection {
		height: auto !important;

		outline: none;

		padding-left: 4px;
		padding-top: 3px;
		padding-bottom: 3px;

		border: 1px solid #cdcdcd !important;
		/* transition: border 150ms ease-in-out; */
	}

	.uap .select2-container--uap-select2 .select2-selection--single {
		border: 1px solid #cdcdcd;
		box-shadow: 0 2px 5px 0 rgba(0,0,0,.1);
		min-height: 40px;
	}

	.uap .select2-container--uap-select2 .select2-selection--single .select2-selection__rendered {
		  color: #444 !important;
	}

	.uap .form-element--error .select2-selection {
		border-color: #e85113 !important;
	}

	.uap .select2-container--uap-select2 .select2-selection--single .select2-selection__arrow b,
	.uap .select2-container--uap-select2.select2-container--open .select2-selection--single .select2-selection__arrow b {
		display: block;
		border: 0;
	}

	.uap .select2-container--uap-select2 .select2-selection--single .select2-selection__arrow {
		right: 7px;
		top: 2px;
	}

	.uap .select2-container--uap-select2 .select2-selection--single .select2-selection__arrow b:before,
	.uap .select2-container--uap-select2.select2-container--open .select2-selection--single .select2-selection__arrow b:before {
		content: '';
		font-family: 'Uncanny Automator Icons';
		right: 5px;
		color: #a2a2a2;
	}

	.uap .select2-container--uap-select2 .select2-selection--single .select2-selection__arrow b {
		margin-top: -2px;
	}

	.uap .select2-container--uap-select2 .select2-selection--single .select2-selection__arrow b:before {
		content: '\f107';
	}

	.uap .select2-container--uap-select2.select2-container--open .select2-selection--single .select2-selection__arrow b:before {
		content: '\f106';
	}

	.select2-container--uap-select2 .select2-dropdown {
		border: 0 !important;
		font-size: 15px;
	}

	.select2-container--uap-select2 .select2-search--dropdown {
		padding: 0 !important;
		box-shadow: 0 2px 5px 0 rgba(0,0,0,.1);
	}

		.select2-container--uap-select2 .select2-search--dropdown .select2-search__field {
			border: 1px solid #cdcdcd !important;
			padding: 10px 15px;
			outline: none;
			font-size: 15px;
			font-family: inherit;
			line-height: 1;
			border-radius: 0;
			margin: 0;
		}

			.select2-container--uap-select2 .select2-search--dropdown .select2-search__field:hover {
				border-color: #b9b9b9 !important;
			}

			.select2-container--uap-select2 .select2-search--dropdown .select2-search__field:focus {
				border-color: #0790e8 !important;
				box-shadow: none;
			}

			.select2-container--uap-select2 .select2-dropdown.select2-dropdown--below .select2-search--dropdown .select2-search__field {
				margin-top: -1px;

				/* WordPress tries to overwrite this */
				margin-left: 0;
				margin-right: 0;
				margin-bottom: 0;
			}

			.select2-container--uap-select2 .select2-dropdown.select2-dropdown--above .select2-search--dropdown .select2-search__field {
				border-top-left-radius: 8px;
				border-top-right-radius: 8px;
			}

		.select2-container--uap-select2 .select2-results {
			border: 1px solid #cdcdcd;
			border-top: 0;
			margin: 0;
			padding: 0;
		}

		.select2-container--uap-select2 .select2-dropdown:not(.select2-dropdown--above) .select2-results {
			border-bottom-right-radius: 8px;
			border-bottom-left-radius: 8px;
		}

		.select2-container--uap-select2 .select2-dropdown.select2-dropdown--above .select2-results {
			border-bottom: 0;
		}

		.select2-container--uap-select2 .select2-results__option[role="treeitem"],
		.select2-container--uap-select2 .select2-results__group {
			/* WordPress tries to overwrite this */
			margin: 0 !important;
		}

			.select2-container--uap-select2 .select2-results__option:not(:last-child){
				border-bottom: 1px solid #cdcdcd;
			}

		// .select2-container--uap-select2 .select2-results__option[] {}

		.select2-container--uap-select2 .select2-results__options[aria-multiselectable="true"] {
			border-top: 1px solid #cdcdcd;
		}

		.select2-container--uap-select2 .select2-results__group {
			padding: 10px 12px 10px 42px !important;
			border-bottom: 1px solid #cdcdcd !important;
			background: #fff !important;

			font-size: 14px !important;
			font-weight: 400 !important;
			color: #444 !important;

			position: relative;
		}

			.select2-container--uap-select2 .select2-results__group:before {
				content: '';

				width: 20px;
				height: 20px;
				border-radius: 50%;
				border: 1px solid #0790e8;
				position: absolute;
				top: 50%;
				left: 12px;
				transform: translateY(-50%);
			}

			.select2-container--uap-select2 .select2-results__group:after {
				content: '\f013';

				width: 22px;
				text-align: center;

				font-family: 'Uncanny Automator Icons';
				speak: none;
				font-style: normal;
				font-weight: normal;
				font-variant: normal;
				text-transform: none;
				line-height: 1;
				-webkit-font-smoothing: antialiased;
				-moz-osx-font-smoothing: grayscale;

				position: absolute;
				transform: translateY(-50%);
				top: 50%;
				left: 12px;

				font-size: 12px;
				color: #0790e8;
			}

		.select2-container--uap-select2 li.select2-results__option[role="group"] ul.select2-results__options .select2-results__option[data-select2-id$="-automator_custom_value"] {
			padding: 10px 12px 10px 42px !important;
			position: relative;
		}

			.select2-container--uap-select2 li.select2-results__option[role="group"] ul.select2-results__options .select2-results__option[data-select2-id$="-automator_custom_value"]:before {
				content: '\f11c';

				width: 22px;
				text-align: center;

				font-family: 'Uncanny Automator Icons';
				speak: none;
				font-style: normal;
				font-weight: normal;
				font-variant: normal;
				text-transform: none;
				line-height: 1;
				-webkit-font-smoothing: antialiased;
				-moz-osx-font-smoothing: grayscale;

				position: absolute;
				transform: translateY(-50%);
				top: 50%;
				left: 12px;

				font-size: 15px;
				color: #969696;
			}

		.select2-container--uap-select2 li.select2-results__option[role="group"] {
			padding: 0 !important;
			margin: 0 !important;
		}

		.select2-container--uap-select2 .select2-results__option {
			color: #565555 !important;
			background: #f3f3f3 !important;
			padding: 10px 12px !important;
			font-size: 15px !important;
			margin: 0 !important;
			min-height: 39px;
			box-sizing: border-box;
		}

		.select2-container--uap-select2 .select2-results>.select2-results__options::-webkit-scrollbar {
			width: 10px;
			background-color: rgba(0,0,0,.09);
			border-radius: 150px;
		}

		.select2-container--uap-select2 .select2-results>.select2-results__options::-webkit-scrollbar-thumb:vertical {
			background: rgba(0,0,0,.3);
			border-radius: 150px;
			background-clip: padding-box;
			border: 2px solid rgba(0,0,0,0);
			min-height: 10px;
		}

		.select2-container--uap-select2 li.select2-results__option[role="group"][title="form-element__select-normal-options"] > strong.select2-results__group {
			display: none !important;
		}

	.select2-container--uap-select2.select2-container--open.select2-container--below .select2-selection--single {
		border-bottom: 0px !important;
		margin-bottom: 1px;
	}

	.select2-container--uap-select2.select2-container--open.select2-container--below .select2-selection--multiple {
		margin-bottom: 0;
		border-bottom: 0px !important;
	}
	
	.select2-container--uap-select2 .select2-results__option:not(.select2-results__option--highlighted) {
		background-color: #f3f3f3 !important;
		color: #565555 !important;
	}

	.select2-container--uap-select2 .select2-results__option--highlighted,
	.select2-container--uap-select2 .select2-results__option--highlighted[aria-selected] {
		background-color: #e3e3e3 !important;
		color: #565555 !important;
	}

	.select2-container--uap-select2 .select2-results__option[aria-selected="true"] {
		background-color: #e3e3e3 !important;
	}

	.uap .form-element--loading .select2-selection {
		background: #ebebeb !important;
		border-color: #cdcdcd !important;
		cursor: not-allowed;
		pointer-events: none; /* Prevent clicks */
	}