.uap-type-selector {
	$section_class: &;

	width: 100%;
	height: 100vh;

	position: fixed;
	top: 0;
	left: 0;

	background: rgba(0,0,0,.4);

	z-index: 100000; /* Something big */

	&__box {
		width: 450px;
		max-width: calc(100% - 40px);
		max-height: calc(100% - 60px);
		overflow: auto;

		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%,-50%);

		/* border: 1px solid #cecece; */
		background: #fff;
		border-radius: 8px;

		padding: 20px;

		box-shadow: 0 2px 10px 0 rgba(0,0,0,.1);

		#{ $section_class }__title {
			font-size: 17px;
			font-weight: 600;
			color: #373737;
			margin-bottom: 15px;
		}

		#{ $section_class }__content {

			#{ $section_class }__types {
				width: auto;

				display: flex;

				margin-left: -10px;
				margin-right: -10px;

				#{ $section_class }-type {
					$type_class: #{ $section_class }-type;

					width: 50%;

					padding-left: 10px;
					padding-right: 10px;

					margin-bottom: 15px;

					&__label {
						display: block;
						height: 100%;

						transition: background 150ms ease-in-out;
					}

					&__container {
						width: 100%;
						height: 100%;

						border: 1px solid rgba(0,0,0,.1);
						border-radius: 8px;
						padding: 15px;

						transition: border 150ms ease-in-out;
					}

					&__icon {
						width: 100%;

						padding-left: 10%;
						padding-right: 10%;

						margin-bottom: 15px;

						&-img {
							width: 100%;
							padding-top: 100%;

							border: 1px solid rgba(0,0,0,.1);
							border-radius: 50%;

							background-repeat: no-repeat;
							background-position: center center;
							background-size: cover;
							background-color: #fff;

							&--user {
								background-image: url( '../img/recipe-type-user.svg' );
							}

							&--anonymous {
								background-image: url( '../img/recipe-type-anonymous.svg' );
							}
						}	
					}

					&__hidden-field {
						display: none;

						&:checked + #{ $type_class }__label {
							background: #f2f9fe;

							#{ $type_class }__container {
								border-color: #0790e8;
							}
						}
					}

					&__title {
						font-weight: 600;
						font-size: 15px;

						margin-bottom: 5px;
					}

					&__description {
						font-size: 14px;
						line-height: 1.3;
						color: rgba(0,0,0,.7);
					}
				}
			}
		}

		#{ $section_class }__warning,
		#{ $section_class }__error {
			padding: 10px 15px;
			border-radius: 8px;
			line-height: 1.5;
			
			margin-bottom: 15px;
		}

		#{ $section_class }__warning {
			color: #000;
			background: #fff3cf;
		}

		#{ $section_class }__error {
			color: #000;
			background: #f9e2e2;
		}

		#{ $section_class }__buttons {
			text-align: right;
		}
	}
}