.uap {
	.form-group {
		width: 100%;

		&:not(:last-child) {
			margin-bottom: 15px;
		}

		&__label {
			width: 100%;
			display: block;
			margin-bottom: 10px;
			font-weight: 600;
			position: relative;
			transition: color 150ms ease-in-out;
		}

		&__fields {
			display: flex;

			border: 1px solid #cdcdcd;
			border-radius: 8px;

			padding: 18px 10px 20px 10px;

			.form-element {
				flex: 1 1 auto;

				padding-left: 10px;
				padding-right: 10px;

				margin-bottom: 0;
			}
		}

		&__description {
			width: 100%;

			margin-top: 7px;

			color: #747474;
    		font-size: 14px;
		}
	}
}