/* Form element */

.uap .form-element {
	width: 100%;
}

	.uap .form-element:after {
		content: '';
		display: block;
		clear: both;
	}

	.uap .form-element:not(:last-child) {
		margin-bottom: 15px;
	}

	.uap .form-element--nomargin {
		margin: 0 !important;
	}

	.uap .form-element--hidden {
		display: none !important;
	}

	.uap .form-element--loading { /* Loading class */ }

	.uap .form-element__label {
		width: 100%;
		display: block;
		margin-bottom: 10px;
		font-weight: 600;
		position: relative;
		transition: color 150ms ease-in-out;
	}

		.uap .form-element__label:empty {
			display: none;
		}

		.uap .form-element--loading .form-element__label:after {
			content: '\e000';
			font-family: 'Uncanny Automator Icons';

			position: absolute;
			color: #666666;
			font-size: 13px;

			animation: rotating 2s linear infinite;

			margin-left: 10px;
		}

		.uap .form-element--error .form-element__label {
			color: #e85113;
		}

	.uap .form-element__input,
	.uap .form-variable__render--input {
		height: 37px;

		min-height: 0 !important;

		padding-left: 12px;
		padding-right: 12px;

		transition: border-color 150ms ease-in-out, box-shadow 150ms ease-in-out;

		border-radius: 8px;
		border: 1px solid #cdcdcd;
		box-shadow: 0 2px 5px 0 rgba(0,0,0,.1) !important;

		outline: none !important;
	}

	.uap .form-element__input-cm {
		position: relative;
	}

	.uap .form-element__input-cm .CodeMirror {
		height: auto;
		min-height: 40px !important;
		line-height: 1.5;
		transition: border-color 150ms ease-in-out, box-shadow 150ms ease-in-out;
		border-radius: 8px;
		border: 1px solid #cdcdcd;
		box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.1) !important;
		outline: none !important;

		padding: 7px 12px 7px 12px;
	}

		.uap .form-element__input-cm .CodeMirror .uap-token {
			margin-top: 2px;
  			margin-bottom: 2px;
		}

		.uap .form-element__input-cm .CodeMirror-placeholder {
			opacity: .4;
			padding: 0;
		}

		.uap .form-element__input-cm .CodeMirror-lines,
		.uap .form-element__input-cm .CodeMirror-line {
			padding: 0;
			color: #444;
		}

		.uap .form-element__input-cm .CodeMirror {
			font-family: inherit;
			font-size: 15px;
		} 

		.uap .form-element__input-cm .CodeMirror-scroll {
			overflow: visible !important;
		}

	.uap .form-element__input {}

		.uap #recipe-triggers-ui .form-element__input,
		.uap #recipe-actions-ui .form-element__input,
		.uap .form-element__input-cm:not(:empty) ~ .form-element__input {
			width: 1px;
			height: 1px;
			padding: 0;
			border: 0;
			position: absolute;
			z-index: -999;
			opacity: 0;
		}

		.uap .form-element__input,
		.uap .form-element__input-cm,
		.uap .form-element__input-cm .CodeMirror {
			width: 100%;
		}

		.uap .form-element__input,
		.uap .form-variable__render--input input {
			padding-top: 8px;
			padding-bottom: 8px;
		}

			.uap .form-element__input:hover,
			.uap .form-variable__render--input:hover,
			.uap .form-element__input-cm .CodeMirror:not(.CodeMirror-focused):hover {
				border-color: #b9b9b9;
			}

			.uap .form-element__input:focus,
			.uap .form-variable__render--input:focus,
			.uap .form-element__input-cm .CodeMirror-focused {
				border-color: #0790e8;
				box-shadow: 0 0 0 2px rgba(#0790e8,.2) !important;
			}

			.uap .form-element--loading .form-element__input,
			.uap .form-element--loading .form-variable__render--input,
			.uap .form-element--loading .form-element__input-cm .CodeMirror {
				background: #ebebeb !important;
				border-color: #cdcdcd !important;
				cursor: not-allowed;
				pointer-events: none; /* Prevent clicks */
			}

			.uap .form-variable__render--input input {
				border: 0;
				outline: none;

				/* */
			}

			.uap .form-element--error .form-element__input,
			.uap .form-element--error .form-variable__render--input,
			.uap .form-element--error .form-element__input-cm .CodeMirror {
				border-color: #e85113 !important;
			}

			.uap .form-element--error .form-element__input:focus,
			.uap .form-element--error .form-variable__render--input:focus,
			.uap .form-element--error .form-element__input-cm .CodeMirror-focused {
				box-shadow: 0 0 0 2px rgba(#e85113,.2) !important;
			}

		.uap .form-element__input-clear {
			color: #e85113;
			cursor: pointer;
			height: 20px;
			position: absolute;
			right: 8px;
			top: 10px;
			width: 20px;
			z-index: 1;
			display: flex;
			align-items: center;
			justify-content: center;
		}

	.uap .form-element__textarea {
		width: 100%;

		min-height: 100px;
		height: 200px;

		padding: 6px 12px;

		resize: vertical;
		line-height: 1.5;

		border-radius: 8px;
		border: 1px solid #cdcdcd;
		transition: border-color 150ms ease-in-out, box-shadow 150ms ease-in-out;

		outline: none;

		box-shadow: 0 2px 5px 0 rgba(0,0,0,.1);
	}

		.uap .form-element__textarea:hover {
			border-color: #b9b9b9;
		}

		.uap .form-element__textarea:focus {
			border-color: #0790e8;
		}

		.uap .form-element--loading .form-element__textarea {
			background: #ebebeb !important;
			border-color: #cdcdcd !important;
			cursor: not-allowed;
			pointer-events: none; /* Prevent clicks */
		}

		.uap .form-element__textarea--hidden {
			display: none;
		}

	.uap .form-element-label__required {
		display: inline-block;
		vertical-align: top;
		font-size: 5px;
		color: #ff6060;
		line-height: 1;
		margin-left: 3px;
	}

		.uap .form-element-label__required:before {
			content: '\f069';
			font-family: 'Uncanny Automator Icons';
		}

	.uap .form-element--readonly {
		border-color: #cdcdcd;
		background: #ebebeb;
		color: #494949;
		cursor: default;
	}

		.uap .form-element--readonly:focus {
			border-color: #b9b9b9;
		}

	.uap .form-element__error {
		margin-top: 10px;
		color: #e85113;
		display: none;
	}

		.uap .form-element__error--show {
			display: block;
		}

	.uap .form-element-children {
		padding-top: 10px;
		padding-left: 25px;
		position: relative;
	}

		.uap .form-element-children:before {
			content: '\f3be';

			color: #888888;

			position: absolute;
			top: 23px;
			left: 1px;

			transform: rotate(90deg) scale(1, -1);
			transform-origin: left top;

			font-family: 'Uncanny Automator Icons' !important;
			speak: none;
			font-style: normal;
			font-weight: normal;
			font-variant: normal;
			text-transform: none;
			line-height: 1;
			-webkit-font-smoothing: antialiased;
			-moz-osx-font-smoothing: grayscale;
		}

	.uap .form-select-custom {
		display: none;
	}

		.uap .form-select-custom--visible {
			display: block;
		}

/* Form  Input */

.uap .form-input {
	width: 100%;
}

	/* Form - Clear */

	.uap .form-input--has-clear .form-element__input-cm .CodeMirror {
		padding-right: 30px !important;
	}

	/* Form  Input URL */

	.uap .form-input--url .form-input-url-notice {
		width: 100%;
		word-break: break-all;

		color: #727272;
		font-size: 14px;

		display: none;
		margin-top: 5px;
	}

		.uap .form-input--url .form-input-url-notice--show {
			display: block;
		}

		.uap .form-input--url .form-input-url-notice__header,
		.uap .form-input--url .form-input-url-notice__url {
			display: inline-block;
		}

		.uap .form-input--url .form-input-url-notice__header {
			margin-right: 5px;
		}

			.uap .form-input--url .form-input-url-notice__header span {
				font-weight: normal;
				font-size: 12px;
			}

		.uap .form-input--url .form-input-url-notice__url {
			margin-top: 3px;
		}

/* Form  Textarea */

.uap .form-textarea {
	width: 100%;
}

.uap .wp-editor-wrap {
	margin-top: -1px; /* Tabs have margin-top 1px */
}

	.uap .wp-editor-wrap .wp-media-buttons button {
		background: #fff;
		box-shadow: none;
		height: 25px;
		line-height: 22px;
		margin-bottom: 0;
		transition: all 150ms ease-in-out;
	}

		.uap .wp-editor-wrap .wp-media-buttons button:active,
		.uap .wp-editor-wrap .wp-media-buttons button:focus {
			margin: 0 !important;
			top: 0;
		}

	.uap .wp-editor-wrap .mce-container,
	.uap .wp-editor-wrap .quicktags-toolbar {
		border-bottom: 0;
		background: #edeff0;
		box-shadow: none;
	}

	.uap .wp-editor-wrap .quicktags-toolbar {
		border-bottom: 1px solid #e2e4e7;
	}

	.uap .wp-editor-wrap div.mce-toolbar-grp>div {
		border-bottom: 1px solid #e2e4e7;
	}

	.uap .wp-editor-wrap .mce-top-part::before {
		display: none;
	}

	.uap .wp-editor-wrap .wp-editor-container {
		outline: none;
		border: 1px solid #cdcdcd;
		box-shadow: 0 2px 5px 0 rgba(0,0,0,.1);
		border-radius: 8px;
		border-top-right-radius: 0;
		overflow: hidden;
	}

	.uap .wp-editor-tabs {}

		.uap .wp-editor-tabs .wp-switch-editor {
			box-sizing: content-box;
			border: 1px solid #cdcdcd;
			border-bottom: 0;
			background: transparent;
		}

			.uap .wp-editor-wrap.tmce-active .wp-switch-editor.switch-tmce,
			.uap .wp-editor-wrap.html-active .wp-switch-editor.switch-html {
				background: #edeff0;
			}

			.uap .wp-editor-wrap .wp-switch-editor.switch-tmce,
			.uap .wp-editor-wrap .wp-switch-editor.switch-html {
				border-top-left-radius: 8px;
				border-top-right-radius: 8px;
			}

			.uap .wp-editor-wrap .wp-switch-editor.switch-html {
				border-top-right-radius: 0px;
			}

			.uap .wp-switch-editor.wp-switch-editor {
				margin-top: 0 !important;
			}

			.uap .mce-btn.mce-active button,
			.uap .mce-btn.mce-active button i {
				color: #333 !important;
			}

			.post-type-uo-recipe .mce-menu .mce-menu-item-normal.mce-active,
			.post-type-uo-recipe .mce-menu .mce-menu-item-preview.mce-active,
			.post-type-uo-recipe .mce-menu .mce-menu-item.mce-selected,
			.post-type-uo-recipe .mce-menu .mce-menu-item:focus,
			.post-type-uo-recipe .mce-menu .mce-menu-item:hover {
				background-color: #0790e8;
			}

			.post-type-uo-recipe .mce-menu .mce-menu-item-normal.mce-active *,
			.post-type-uo-recipe .mce-menu .mce-menu-item-preview.mce-active *,
			.post-type-uo-recipe .mce-menu .mce-menu-item.mce-selected *,
			.post-type-uo-recipe .mce-menu .mce-menu-item:focus *,
			.post-type-uo-recipe .mce-menu .mce-menu-item:hover * {
				color: #fff;
			}

		.uap .wp-editor-wrap textarea {
			border: 0;
			box-shadow: none;
		}

/* Form  Select */

.uap .form-select {
	width: 100%;

	
}

/* Form  Checkbox */

.uap .form-checkbox {
	width: 100%;
	min-height: 20px;
}

	.uap .form-element--loading .form-checkbox {
		opacity: .6;
		cursor: not-allowed;
		pointer-events: none; /* Prevent clicks */
	}

	.uap .form-checkbox__container {
		display: block;
		position: relative;
		padding-left: 23px;
		cursor: pointer;
		transition: opacity 150ms ease-in-out;
	}

		.uap .form-checkbox__container:not(:last-child){
			margin-bottom: 10px;
		}

	.uap .checkbox--hidden {
		display: none;
	}

	/* Normal checkbox [] */

	.uap .form-checkbox:not(.form-checkbox--toggle) .checkbox--show {
		box-sizing: border-box;
		position: absolute;
		top: 2px;
		left: 0;
		height: 16px;
		width: 16px;
		border: 1px solid #afafaf;
		border-radius: 4px;
		transition: all 150ms ease-in-out;
		box-shadow: 0 2px 5px 0 rgba(0,0,0,.1);
	}

	.uap .form-checkbox:not(.form-checkbox--toggle) .form-checkbox__container:hover .checkbox--show {
		border-color: #979797;
	}

	.uap .form-checkbox:not(.form-checkbox--toggle) .checkbox--show:after {
		content: '';
		position: absolute;
		display: block;
		opacity: 0;
		transition: all 150ms ease-in-out;
	}

	.uap .form-checkbox:not(.form-checkbox--toggle) .checkbox--hidden:checked ~ .checkbox--show:after {
		opacity: 1;
	}

	.uap .form-checkbox:not(.form-checkbox--toggle) .form-checkbox__container .checkbox--show:after {
		left: 4px;
		top: 1px;
		width: 4px;
		height: 8px;
		border: solid #0790e8;
		border-width: 0 2px 2px 0;
		transform: rotate(45deg);
	}

	.uap .form-checkbox:not(.form-checkbox--toggle) .checkbox--primary:checked ~ .checkbox--show {
		border-color: #0790e8;
	}

	.uap .form-checkbox:not(.form-checkbox--toggle) .checkbox--primary:disabled ~ .checkbox--show {
		background: #ebebeb;
		border: 1px solid #cdcdcd;
		cursor: not-allowed;
	}

	.uap .form-checkbox:not(.form-checkbox--toggle) .checkbox--primary:disabled ~ .checkbox--show:after {
		border-color: #494949 !important;
	}

	.uap .form-checkbox--loading-inline .form-checkbox__container {
		cursor: not-allowed;
		pointer-events: none;
		/* opacity: .7; */
	}

		.uap .form-checkbox--loading-inline .form-checkbox__container:after {
			content: '\e000';
			font-family: 'Uncanny Automator Icons';

			position: absolute;
			color: #666666;
			font-size: 13px;

			animation: rotating 2s linear infinite;

			margin-left: 10px;
		}

	/* Toggle  */

	.uap .form-checkbox.form-checkbox--toggle .form-checkbox__container {
		padding-left: 35px;
	}

	.uap .form-checkbox.form-checkbox--toggle .checkbox--show {
		width: 30px;
		height: 16px;
		position: absolute;
		top: 2px;
		left: 0;
	}

	.uap .form-checkbox.form-checkbox--toggle .checkbox--show:before {
		content: '';
		width: 100%;
		height: 100%;
		border-radius: 150px;
		border: 2px solid #c8c8c8;
		display: block;
		position: absolute;
		box-sizing: border-box;
		z-index: 1;

		transition: border 150ms ease-in-out, background 150ms ease-in-out;
	}

		.uap .form-checkbox.form-checkbox--toggle.form-checkbox--toggle-gutenberg .checkbox--show:before {
			border-color: #6c7781;
		}

	.uap .form-checkbox.form-checkbox--toggle .checkbox--show:after {
		content: '';
		width: 8px;
		height: 8px;
		background: #afafaf;
		border-radius: 50%;
		display: block;

		position: absolute;
		top: 50%;
		left: 5px;
		transform: translateY(-50%);
		z-index: 2;

		transition: left 150ms ease-in-out, background 150ms ease-in-out;
	}

		.uap .form-checkbox.form-checkbox--toggle.form-checkbox--toggle-gutenberg .checkbox--show:after {
			background: #6c7781;
		}

	/* Blue toggle */

	.uap .form-checkbox.form-checkbox--toggle .checkbox--hidden:checked ~ .checkbox--show:before {
		background: #0790e8;
		border-color: #0790e8;
	}

	.uap .form-checkbox.form-checkbox--toggle .checkbox--hidden:checked ~ .checkbox--show:after {
		background: #fff;
		left: 18px;
		box-shadow: 0 0 10px 1px rgba(0, 0, 0, .1);
	}

	/* Green toggle */

	.uap .form-checkbox.form-checkbox--toggle.form-checkbox--toggle-green .checkbox--hidden:checked ~ .checkbox--show:before {
		background: #6bc45a;
		border-color: #6bc45a;
	}

	/* Text in toggle */

	.uap .form-checkbox .checkbox-label {
		display: none;
		font-weight: 600;
		user-select: none;
		transition: opacity 150ms ease-in-out;
	}

		.uap .checkbox-label.checkbox-label--off {
			color: #888888;
		}

		.uap .form-checkbox.form-checkbox--toggle.form-checkbox--toggle-gutenberg .checkbox-label.checkbox-label--off {
			color: #6c7781;
		}			

	.uap .checkbox-label.checkbox-label--on {
		color: #0790e8;
	}

		.uap .form-checkbox.form-checkbox--toggle.form-checkbox--toggle-green .checkbox-label.checkbox-label--on {
			color: #5faa51;
		}

	.uap .checkbox--hidden:not(:checked) ~ .checkbox-label.checkbox-label--off {
		display: block;
	}

	.uap .checkbox--hidden:checked ~ .checkbox-label.checkbox-label--on {
		display: block;
	}

	/* Toggle on the right, with label on right */

	.uap .form-checkbox--toggle-right {
		text-align: right;
	}

		.uap .form-checkbox--toggle-right .form-checkbox__container {
			padding-left: 0 !important;
			padding-right: 35px;
		}

		.uap .form-checkbox--toggle-right .checkbox--show {
			left: auto !important;
			right: 0;
		}

		.uap .form-checkbox--loading-inline.form-checkbox--toggle-right .form-checkbox__container:after {
			right: -20px;
			top: 1px;
		}

/* Form  Radio */

.uap .form-radio {
	width: 100%;
}

	.uap .form-radio__container {
		display: block;
		position: relative;
		padding-left: 23px;
		cursor: pointer;

		min-height: 20px;

		line-height: 1.1;
	}

		.uap .form-radio__container:not(:last-child){
			margin-bottom: 10px;
		}

		.uap .form-radio__container {
			margin-bottom: 10px !important;
		}

	.uap .radio--hidden {
		display: none;
	}

	.uap .radio--show {
		box-sizing: border-box;
		position: absolute;
		top: 0px;
		left: 0;
		height: 16px;
		width: 16px;
		border: 1px solid #afafaf;
		border-radius: 50%;
		transition: all 150ms ease-in-out;
		box-shadow: 0 2px 5px 0 rgba(0,0,0,.1);
	}

	.uap .form-radio__container:hover .radio--show {
		border-color: #979797;
	}

	.uap .radio--show:after {
		content: '';
		position: absolute;
		display: block;
		opacity: 0;
		transition: all 150ms ease-in-out;
	}

	.uap .radio--hidden:checked ~ .radio--show:after {
		opacity: 1;
	}

	.uap .form-radio__container .radio--show:after {
		left: 3px;
		top: 3px;
		width: calc(100% - 6px);
		height: calc(100% - 6px);
		background: #0790e8;
		border-radius: 50%;
	}

	.uap .radio--primary:checked ~ .radio--show {
		border-color: #0790e8;
	}

	.uap .radio--primary:disabled ~ .radio--show {
		background: #fff;
		border: 1px solid #b9b9b9;
		cursor: not-allowed;
	}

	.uap .radio--primary:checked:disabled ~ .radio--show {
		background: #38aece;
		opacity: 0.6;
		cursor: not-allowed;
	}

/* Form  Variable */

.uap .form-variable {
	width: 100%;
	position: relative;
}

	.uap .form-variable__element {
		width: 100%;
		position: relative;

		display: flex;
	}

		.uap .form-variable__render-container,
		.uap .form-variable__toggle {}

		.uap .form-variable__toggle {
			display: flex;
			align-items: center;
			justify-content: center;
		}

		.uap .form-variable__element.form-variable__element--empty .form-variable__render-container {
			width: 100%;
		}

		.uap .form-variable__element:not(.form-variable__element--empty) .form-variable__render-container {
			width: calc(100% - 35px);
		}

			/* Input */

			.uap .form-variable__element:not(.form-variable__element--empty) .form-element__input,
			.uap .form-variable__element:not(.form-variable__element--empty) .form-element__input-cm .CodeMirror {
				border-top-right-radius: 0px !important;
				border-bottom-right-radius: 0px !important;
			}

			.uap .form-variable--open.form-variable--below .form-variable__element:not(.form-variable__element--empty) .form-element__input,
			.uap .form-variable--open.form-variable--below .form-variable__element:not(.form-variable__element--empty) .form-element__input-cm .CodeMirror {
				border-bottom-left-radius: 0px !important;
				/* border-bottom: 0px !important; */
			}

			.uap .form-variable--open.form-variable--above .form-variable__element:not(.form-variable__element--empty) .form-element__input,
			.uap .form-variable--open.form-variable--above .form-variable__element:not(.form-variable__element--empty) .form-element__input-cm .CodeMirror {
				border-top-left-radius: 0px !important;
				/* border-top: 0px !important; */
			}

			/* Variables */

