			}

	.uap .form-variable__dropdown {
		position: absolute;
		width: calc( 100% - 35px );
		z-index: 10;
		display: none;
	}

	.uap .form-variable--open .form-variable__dropdown {
		display: block;
	}

		.uap .form-variable--below .form-variable__dropdown {
			/* bottom: 0; */
			top: calc(100% - 1px);
		}

		.uap .form-textarea .form-variable--below .form-variable__dropdown {
			top: 38px;
		}

		.uap .form-variable--above .form-variable__dropdown {
			top: 0;
		}

			.uap .form-variable__dropdown-container {
				width: 100%;
				position: absolute;
				box-shadow: 0 15px 30px 0 rgba(0,0,0,.2);
			}

				.uap .form-variable--below .form-variable__dropdown-container {
					position: absolute;
					top: 0;
					border-bottom-left-radius: 8px;
					border-bottom-right-radius: 8px;
				}

				.uap .form-variable--above .form-variable__dropdown-container {
					bottom: 0;
					border-top-left-radius: 8px;
					border-top-right-radius: 8px;
				}

			.uap .form-variable__dropdown-container .form-variable__search {
				width: 100%;
				position: relative;
			}

				.uap .form-variable__dropdown-container .form-variable__search-input {
					width: 100%;
					padding: 10px 12px;

					line-height: 1;

					font-size: 14px;

					border: 1px solid #cdcdcd;
					/* transition: all 150ms ease-in-out; */

					outline: none;

					box-shadow: 0 2px 5px 0 rgba(0,0,0,.1);
				}

					.uap .form-variable__dropdown-container .form-variable__search-input:hover {
						border-color: #b9b9b9;
