						cursor: pointer;

						font-size: 14px;

						position: relative;

						transition: background 150ms ease-in-out;
					}

						.uap .form-variable__group-item:hover {
							background: #e3e3e3;
						}

						.uap .form-variable__group-item-no-results {
							border-top: 0;
							cursor: default;
						}

