#recipe_categorydiv {
	.selectit {
		width: auto;
		position: relative;
		display: block;

		padding-top: 2px;
		padding-bottom: 2px;
		padding-left: 23px;
		padding-right: 25px;

		transition: opacity 150ms ease-in-out;

		// animation: placeHolderShimmer 1s steps(10) 1s infinite alternate;

		line-height: 1.4;

		input {
			position: absolute;
			top: 7px;
			left: 0px;

			&:disabled {
				border: 1px solid #7e8993;
			}
		}

		&:before {
			content: '\e000';
			font-family: 'Uncanny Automator Icons';

			opacity: 0;

			position: absolute;
			top: 10px;
			right: -6px;
			z-index: 1;

			color: #8e8e8e;
			font-size: 15px;

			animation: rotating-centered 2s linear infinite;

			transition: opacity 150ms ease-in-out;
		}

		&:after {
			font-family: 'Uncanny Automator Icons';

			opacity: 0;

			position: absolute;
			top: 10px;
			right: -6px;
			z-index: 1;
			transform: translate(-50%,-50%);

			font-size: 15px;

			transition: opacity 150ms ease-in-out;
		}
	}
	
	.uap-taxonomy-loading {
		pointer-events: none;
		cursor: default;

		opacity: .8;

		&:before {
			opacity: 1;
		}
	}

	.uap-taxonomy-success {
		&:after {
			opacity: 1;

			content: '\f00c';
			color: #6bc45a;
		}
	}

	.uap-taxonomy-error {
		&:after {
			opacity: 1;

			content: '\f00d';
			color: #e85113;
		}
	}
}

#tagsdiv-recipe_tag {
	.ui-sortable-handle span {
		position: relative;

		&:before {
			content: '\e000';
			font-family: 'Uncanny Automator Icons';

			opacity: 0;

			position: absolute;
			top: 10px;
			right: -32px;
			z-index: 1;

			color: #8e8e8e;
			font-size: 15px;

			animation: rotating-centered 2s linear infinite;

			transition: opacity 150ms ease-in-out;
		}

		&:after {
			font-family: 'Uncanny Automator Icons';

			opacity: 1;

			position: absolute;
			top: 10px;
			right: -32px;
			z-index: 1;
			transform: translate(-50%,-50%);

			font-size: 15px;

			transition: opacity 150ms ease-in-out;
		}
	}

	.inside {
		transition: opacity 150ms ease-in-out;
		opacity: 1;
	}

	&.uap-tags-loading {
		.inside {
			pointer-events: none;
			cursor: default;
			opacity: .8;
		}

		.ui-sortable-handle span:before {
			opacity: 1;
		}
	}

	&.uap-taxonomy-success {
		.ui-sortable-handle span:after {
			opacity: 1;

			content: '\f00c';
			color: #6bc45a;
		}
	}

	&.uap-taxonomy-error {
		.ui-sortable-handle span:after {
			opacity: 1;

			content: '\f00d';
			color: #e85113;
		}
	}
}