/*
* Tooltip
*/

[uap-tooltip] {
	position: relative; 
}

[uap-tooltip]::before,
[uap-tooltip]::after {
	text-transform: none;
	font-size: 13px !important;
	font-weight: 500;
	line-height: 1;
	user-select: none;
	pointer-events: none;
	position: absolute;
	display: none;
	opacity: 0;
}

[uap-tooltip]::before {
	content: '';
	border: 5px solid transparent;
	z-index: 1001;
}

[uap-tooltip]::after {
	width: 100%;
	content: attr(uap-tooltip);
	text-align: center;
	min-width: 4em;
	max-width: 21em;
	/*white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;*/
	padding: 6px 10px 8px 10px;
	line-height: 1.4;
	border-radius: 8px;
	box-shadow: 0 1em 2em -.5em rgba(0, 0, 0, 0.35);
	background: #333;
	color: #fff;
	z-index: 1000;
}

[uap-tooltip][uap-flow^="left"]::after {
	min-width: 100px;
}

[uap-tooltip]:hover::before,
[uap-tooltip]:hover::after {
	display: block;
}

[uap-tooltip='']::before,
[uap-tooltip='']::after {
	display: none !important;
}

[uap-tooltip]:not([uap-flow])::before,
[uap-tooltip][uap-flow^="up"]::before {
	bottom: calc(100% + 5px);
	border-bottom-width: 0;
	border-top-color: #333;
}
[uap-tooltip]:not([uap-flow])::after,
[uap-tooltip][uap-flow^="up"]::after {
	bottom: calc(100% + 10px);
}
[uap-tooltip]:not([uap-flow])::before,
[uap-tooltip]:not([uap-flow])::after,
[uap-tooltip][uap-flow^="up"]::before,
[uap-tooltip][uap-flow^="up"]::after {
	left: 50%;
	transform: translate(-50%, -.5em);
}

[uap-tooltip][uap-flow^="down"]::before {
	top: 100%;
	border-top-width: 0;
	border-bottom-color: #333;
}
[uap-tooltip][uap-flow^="down"]::after {
	top: calc(100% + 5px);
}
[uap-tooltip][uap-flow^="down"]::before,
[uap-tooltip][uap-flow^="down"]::after {
	left: 50%;
	transform: translate(-50%, .5em);
}
[uap-tooltip][uap-flow^="left"]::before {
	top: 50%;
	border-right-width: 0;
	border-left-color: #333;
	left: calc(0em - 5px);
	transform: translate(-.5em, -50%);
}
[uap-tooltip][uap-flow^="left"]::after {
	top: 50%;
	right: calc(100% + 5px);
	transform: translate(-.5em, -50%);
}
[uap-tooltip][uap-flow^="right"]::before {
	top: 50%;
	border-left-width: 0;
	border-right-color: #333;
	right: calc(0em - 5px);
	transform: translate(.5em, -50%);
}
[uap-tooltip][uap-flow^="right"]::after {
	top: 50%;
	left: calc(100% + 5px);
	transform: translate(.5em, -50%);
}

[uap-tooltip]:not([uap-flow]):hover::before,
[uap-tooltip]:not([uap-flow]):hover::after,
[uap-tooltip][uap-flow^="up"]:hover::before,
[uap-tooltip][uap-flow^="up"]:hover::after,
[uap-tooltip][uap-flow^="down"]:hover::before,
[uap-tooltip][uap-flow^="down"]:hover::after {
	animation: uap-tooltips-vert 300ms ease-out forwards;
}

[uap-tooltip][uap-flow^="left"]:hover::before,
[uap-tooltip][uap-flow^="left"]:hover::after,
[uap-tooltip][uap-flow^="right"]:hover::before,
[uap-tooltip][uap-flow^="right"]:hover::after {
	animation: uap-tooltips-horz 300ms ease-out forwards;
}

@keyframes uap-tooltips-vert {
	to {
		opacity: .9;
		transform: translate(-50%, 0);
	}
}

@keyframes uap-tooltips-horz {
	to {
		opacity: .9;
		transform: translate(0, -50%);
	}
}

/*
* Update title
*/

#titlediv {
	position: relative;
}

#titlediv #title-prompt-text,
#titlediv #title {
	font-size: 18px;
	font-weight: 600;
	font-family: -apple-system, system-ui, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif;
	padding-left: 12px;
	padding-right: 12px;
	margin-bottom: 0 !important;
}

#titlediv #title-prompt-text {
	margin-top: 1px;
}

#titlediv #title {
	height: 38px;
	box-shadow: none;
	border-radius: 8px;
}

.uap-title-status {}

	.uap-title-status .uo-status {
		display: none;

		position: absolute;
		top: 50%;
		right: 10px;
		transform: translateY(-50%);
		z-index: 10;

		border: 1px solid transparent;
		border-radius: 8px;

		font-size: 14px;

		padding: 2px 10px 2px 26px;
	}

		.uap-title-status .uo-status--saving {
			border-color: #a8a8a8;
			color: #a8a8a8;
		}

		.uap-title-status .uo-status--saved {
			border-color: #52ac57;
			color: #52ac57;
		}

		.uap-title-status .uo-status-icon {
			position: absolute;

			left: 7px;
			top: 6px;

			font-size: 12px;
		}

/*
* Report a bug
*/

.uap-report-bug {
	position: relative;
	z-index: 100000;

	user-select: none;
	font-size: 15px;
	font-family: -apple-system, system-ui, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif;
	-webkit-font-smoothing:antialiased;
	-moz-osx-font-smoothing:grayscale;
}

.uap-report-bug * {
	box-sizing: border-box;
}

	.uap-report-bug__overlay {
		position: fixed;
		width: 100%;
		height: 100%;
		background: rgba(255,255,255,.5);
		z-index: 100001;
		top: 0;
		left: 0;
	}

		.uap-report-bug-circle {
			width: 800px;
			height: 800px;
			border-radius: 50%;

			position: absolute;
			right: 0;
			bottom: -25px;
			transform: translate(50%,50%);

			background: #0790e8;
		}

			.uap-report-bug-circle__container {
				width: 50%;
				height: 50%;

				position: absolute;
				top: 0;
				left: 0;

				padding: 80px 20px 120px 20px;

				text-align: right;
			}

				.uap-report-bug-circle__text {
					width: 100%;
					color: #fff;
					font-size: 22px;
					line-height: 1.3;
					padding-left: 40%;
				}

				.uap-report-bug-circle__dont-show {
					color: #fff;
					font-size: 17px;
					line-height: 1.3;
					padding: 25px 0 0 0;
					text-align: right;
				}

					.uap-report-bug-circle-checkbox {
						display: block;
						position: relative;
						padding-left: 23px;
						cursor: pointer;
					}

						.uap-report-bug-circle-checkbox__input {
							display: none !important;
						}

						.uap-report-bug-circle-checkbox__render {
							box-sizing: border-box;
							height: 16px;
							width: 16px;
							border-radius: 8px;
							transition: all 150ms ease-in-out;
							background: #fff;

							position: relative;
							display: inline-block;
							vertical-align: middle;
						}

						.uap-report-bug-circle-checkbox:hover .uap-report-bug-circle-checkbox__render {
							border-color: #b9b9b9;
						}

							.uap-report-bug-circle-checkbox__render:after {
								content: '';
								position: absolute;
								display: block;
								opacity: 0;
								transition: all 150ms ease-in-out;

								left: 5px;
								top: 2px;
								width: 3px;
								height: 7px;
								border: solid #0790e8;
								border-width: 0 3px 3px 0;
								transform: rotate(45deg);
							}

								.uap-report-bug-circle-checkbox__input:checked ~ .uap-report-bug-circle-checkbox__render:after {
									opacity: 1;
								}

								.uap-report-bug-circle-checkbox__input:checked ~ .uap-report-bug-circle-checkbox__render {
									border-color: #0790e8;
								}

						.uap-report-bug-circle__span {
							vertical-align: middle;
							margin-left: 10px;
						}

				.uap-report-bug-circle__footer {
					width: 100%;
					margin-top: 15px;
					text-align: right;
				}

					.uap-report-bug-circle__btn {
						color: #fff;
						background: #60b051;
						font-weight: 600;
						font-size: 16px;

						cursor: pointer;

						padding: 6px 20px;
						border-radius: 8px;

						transition: background 150ms ease-in-out;

						display: inline-block;
					}

					.uap-report-bug-circle__btn:hover {
						background: #54a246;
					}

	.uap-report-bug__btn {
		position: fixed;
		z-index: 100002;
		
		bottom: 20px;
		right: 20px;

		display: block;

		background: #fff;

		width: 45px;
		height: 45px;
		border-radius: 50%;

		color: #6c757d;
		box-shadow: 0 2px 5px 0 rgba(0,0,0,.1);
		border: 1px solid #b7b7b7;

		transition: border 150ms ease-in-out, box-shadow 150ms ease-in-out, color 150ms ease-in-out;
		outline: none;
	}

		.uap-report-bug--open .uap-report-bug__btn {
			border: 0;
			box-shadow: none;
		}

		.uap-report-bug__btn:hover {
			color: #0790e8;
			border-color: #0790e8;
		}

		.uap-report-bug__btn:active,
		.uap-report-bug__btn:focus {
			color: #0790e8;
			box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, .5);
		}

		.uap-report-bug__icon:before {
			content: '\f0eb';
			font-family: 'Uncanny Automator Icons' !important;
			speak: none;
			font-style: normal;
			font-weight: normal;
			font-variant: normal;
			text-transform: none;
			line-height: 1;
			-webkit-font-smoothing: antialiased;
			-moz-osx-font-smoothing: grayscale;

			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%,-50%);

			font-size: 23px;
		}

/*
* Modal
*/

.uap .modal {
	width: 100vw;
	height: 100vh;

	position: fixed;
	top: 0;
	left: 0;

	overflow-y: visible;

	background: rgba(0,0,0,.4);

	z-index: 100000; /* something big */
}

html.automator-modal-open body {
	overflow: hidden;
}

	.uap .modal__box {
		width: 350px;
		max-width: calc(100% - 40px);
		max-height: calc(100% - 60px);
		overflow: auto;

		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%,-50%);

		/* border: 1px solid #cecece; */
		background: #fff;
		border-radius: 8px;

		padding: 20px;

		box-shadow: 0px 4px 16px 0px rgba(0, 0, 0, 0.2);
	}

		.uap .modal--large .modal__box {
			width: 450px;
		}

		.uap .modal--extra-large .modal__box {
			width: 650px;
		}

		.uap .modal__title {
			font-size: 17px;
			font-weight: 600;
			color: #373737;
			margin-bottom: 15px;
		}

			.uap .modal__title .modal__title-icon {
				color: #9f6000;
			}

		.uap .modal__warning {
			color: #000;
			background: #fff3cf;
			padding: 10px 15px;
			border-radius: 8px;
			line-height: 1.5;

			margin-bottom: 15px;
		}

		.uap .modal__content {
			font-size: 16px;
			line-height: 1.4;
			margin-bottom: 15px;

			width: auto;
			max-height: 200px;
			overflow-y: auto;

			margin-left: -10px;
			margin-right: -10px;
			padding: 0 10px;
		}

			.uap .modal__content,
			.uap .modal__content p {
				font-size: 16px;
				line-height: 1.5;
				margin-top: 0;
			}

			.uap .modal__content p:last-child {
				margin-bottom: 0;
			}

			.uap .modal__content p:not(:last-child) {
				margin-bottom: 10px;
			}

			.uap .modal__content::-webkit-scrollbar {
				width: 10px;
				background-color: rgba(0,0,0,.09);
			}

			.uap .modal__content::-webkit-scrollbar-thumb:vertical {
				background: rgba(0,0,0,.3);
				border-radius: 150px;
				background-clip: padding-box;
				border: 2px solid rgba(0,0,0,0);
				min-height: 10px;
			}

			.uap .modal__content table {
				width: 100%;

				border-spacing: 0;

				border: 1px solid #e0e0e0;
				border-radius: 8px;
			}

				.uap .modal__content table th {
					border-bottom: 1px solid #e0e0e0;

					text-align: left;
					color: #373737;
					font-weight: 600;
					font-size: 14px;

					padding: 10px;

					border-left: 1px solid #e0e0e0;
				}

				.uap .modal__content table td {
					text-align: left;
					color: #373737;
					font-weight: 400;
					font-size: 15px;

					vertical-align: top;

					padding: 10px;

					border-bottom: 1px solid #e0e0e0;
					border-left: 1px solid #e0e0e0;

					word-break: break-all;
				}

					.uap .modal__content table th:first-child,
					.uap .modal__content table td:first-child {
						border-left: 0;
					}

					.uap .modal__content tbody tr:last-child td {
						border-bottom: 0;
					}

			.uap .modal__content .modal__content-code pre {
				width: 100%;

				background: #23241f;
				color: #f8f8f2;

				font-size: 14px;
				overflow-y: auto;

				padding: 15px;
			}

		.uap .modal__buttons {
			text-align: right;

			padding-top: 15px;
		}

			.uap .modal__buttons > .uap-btn:not(:last-child) {
				margin-right: 10px;
			}

			.uap .modal__buttons > .uap-btn:last-child {
				margin-right: 0;
			}

