	/* Item actions */

	.uap .item-actions {
		position: absolute;

		user-select: none;

		top: -15px;
		right: -8px;

		z-index: 10;
	}

		.uap .item-actions__btn {
			color: #b2b2b2;
			font-size: 18px;
			line-height: 1;

			padding: 10px; /* Just to make it easier to click */
			cursor: pointer;

			transition: color 150ms ease-in-out;
		}

			.uap .item-actions--open .item-actions__btn,
			.uap .item-actions__btn:hover {
				color: #6b6b6b;
			}

		.uap .item-actions__dropdown {
			position: absolute;
			bottom: 0;
			right: 2px;
			display: none;
		}

		.uap .item-actions--open .item-actions__dropdown {
			display: block;
		}

			.uap .item-actions-dropdown__ul {
				position: absolute;
				top: 0;
				right: 0;

				display: block;

				margin: 0; /* Reset browser margin */
				padding: 0; /* Reset browser padding */

				list-style: none;

				background: #fff;

				border: 1px solid #c7c7c7;
				border-radius: 8px;

				box-shadow: 0 5px 15px 0 rgba(0,0,0,.2);
			}

				.uap .item-actions-dropdown__ul:before,
				.uap .item-actions-dropdown__ul:after {
					content: '';

					width: 0;
					height: 0;

					position: absolute;
				}

				.uap .item-actions-dropdown__ul:before {
					z-index: 2;

					top: -8px;
					right: 9px;

					border-left: 8px solid transparent;
					border-right: 8px solid transparent;
					border-bottom: 8px solid #fff;
				}

				.uap .item-actions-dropdown__ul:after {
					z-index: 1;

					top: -9px;
					right: 7px;

					border-left: 10px solid transparent;
					border-right: 10px solid transparent;
					border-bottom: 9px solid #c7c7c7;
				}

			.uap .item-actions-dropdown__action {
				padding: 0;	/* Reset WP padding */
				margin: 0;	/* Reset WP margin */
				position: relative;
			}

				.uap .item-actions-dropdown__action:not(:last-child) {
					border-bottom: 1px solid #c9c9c9;
				}

				.uap .item-actions-dropdown__action:hover {}

				.uap .item-actions-dropdown__action a,
				.uap .item-actions-dropdown__action span {
					color: #3c3c3c;
					
					max-width: 100%;
					min-width: 100px;

					cursor: pointer;
					display: block;

					padding: 7px 10px;

					transition: color 150ms ease-in-out;
				}

					.uap .item-actions-dropdown__action a:hover,
					.uap .item-actions-dropdown__action span:hover {
						color: #0790e8;
					}

					.uap .item-actions-dropdown__action:after {
						content: '';
						opacity: 0;
						transition: opacity 150ms ease-in-out;
					}

					/* Loading */

					.uap .item-actions-dropdown__action--loading {
						pointer-events: none;
						cursor: default;
					}

					.uap .item-actions-dropdown__action--loading a,
					.uap .item-actions-dropdown__action--loading span {
						color: #8e8e8e;
					}

					.uap .item-actions-dropdown__action--loading:after {
						opacity: 1;

						content: '\e000';
						font-family: 'Uncanny Automator Icons';

						position: absolute;
						top: 50%;
						right: 3px;
						z-index: 1;

						color: #8e8e8e;
						font-size: 15px;

						animation: rotating-centered 2s linear infinite;
					}

	/* Item icon */

	.uap .item-icon {
		pointer-events: none; /* Disable user interactions */
		
		width: 45px;
		height: 45px;

		background: #fff;
		border-radius: 50%;
		border: 1px solid #cdcdcd;

		position: absolute;

		top: 0;
		left: 0;

		overflow: hidden;

		transition: all 150ms ease-in-out;
		box-shadow: 0 2px 5px 0 rgba(0,0,0,.1);

		user-select: none;
	}

		.uap .item-icon:not(:empty){
			cursor: pointer;
		}

