			border-color: #0790e8;
		}

		.uap .item-icon:not(:empty):active {
			box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, .5);
		}

		.uap .item-icon:before {
			content: '\f0e7'; /* uo-icon--bolt */
			color: #c4c4c4;
			font-family: 'Uncanny Automator Icons';
			font-size: 20px;

			opacity: 0;

			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%,-50%);

			transition: opacity 150ms ease-in-out;
