		position: relative;
	}

		.uap .item-integration:hover,
		.uap .item-integration.item-integration--selected {
			border-color: #0790e8;
		}

		/* .uap .item-integration:active {
			box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, .5);
		} */

		/* .uap .item-integration:not(:last-child){
			margin-right: 10px;
		} */

		.uap .item-integration--unavailable {
				background: #f3f3f3;
			}

			.uap .item-integration--unavailable:hover {
				border-color: #b7b7b7;
			}

			.uap .item-integration--unavailable .item-integration__logo {
				opacity: .6;
				filter: saturate(0);
			}

		.uap .item-integration__logo {
			height: 60px;

			display: flex;
			align-items: center;
			justify-content: center;

			position: relative;
			margin-top: 3px;
		}

			.uap .item-integration__logo img {
				width: 40px;
				height: 40px;

				display: block;
			}

		.uap .item-integration__name {
			padding: 0 10px;

			font-size: 13px;
			font-weight: 600;

			color: #6c6c6c;
			text-align: center;

			white-space: nowrap;
			overflow: hidden;
			text-overflow: ellipsis;
		}

		.uap .item-integration__badge {
			position: absolute;
			top: 10px;
			right: 10px;
			z-index: 10;

			font-size: 13px;
			font-weight: 600;
			line-height: 1;

			color: #fff;
			background: #6bc45a;

			border-radius: 150px;

			padding: 3px 6px;
		}

			.uap .item-integration--available .item-integration__badge {
				background: #a4a4a4;
			}

	/* Item options */

	.uap .item-options {
		width: 100%;
		background: #fff;
		border: 1px solid #cdcdcd;
		border-radius: 8px;
		padding: 20px;
		transition: border 150ms ease-in-out;
	}

		.uap .item-options--nopadding {
			padding: 0px;
		}

		.uap .item-options--noborder {
			border: 0px;
		}

		.uap .item-options--error {
			/* VALIDATION ERROR */
		}

	/* Item options - Notice */

	.uap .item-options__notice {
		color: #383d41;
		background:	#e2e3e5;

		padding: 10px 15px;
		border-radius: 8px;
		line-height: 1.5;

		margin-bottom: 15px;
	}

		.uap .item-options__notice a {
			color: #2c3033;
			border-bottom: 1px solid rgba(44, 48, 51, 0.6);
		}

		.uap .item-options__notice--info {
			background: #bde5f8;
			color: #00529b;
		}

			.uap .item-options__notice--info a {
				color: #003d73;
				border-bottom-color: rgba(0, 61, 115, 0.6);
			}

		.uap .item-options__notice--warning {
			color: #000;
			background: #fdf8d9;
		}

			.uap .item-options__notice--warning a {
				color: #000;
				font-weight: 500;
				border-bottom-color: rgba(0, 0, 0, 0.6);
			}

		.uap .item-options__notice--error {
			background: #f9e2e2;
			color: #000;
		}

			.uap .item-options__notice--error a {
				color: #000;
				font-weight: 500;
				border-bottom-color: rgba(0, 0, 0, 0.6);
			}

		.uap .item-options__notice--success {
			background: #ecf7ed;
			color: #000;
		}

			.uap .item-options__notice--success a {
				color: #000;
				font-weight: 500;
				border-bottom-color: rgba(0, 0, 0, 0.6);
			}

		.uap .metabox__notice p {
			font-family: inherit;
			font-size: inherit;
			margin: 0;
			padding: 0;
		}

	/* Buttons */

	.uap .item-options__buttons {
		width: 100%;
		display: flex;
	}

		.uap .item-options__buttons .item-options__automator-buttons {}

		.uap .item-options__buttons .item-options__developer-buttons {
			text-align: right;
			flex-grow: 1;
		}

		.uap .item-options__buttons .uap-btn:not(:last-child){
			margin-right: 10px;
		}

	/* Item Task selector */

	.uap .item__task-selector {
		width: 100%;
		border-radius: 8px;
	}

		.uap .item__task-selector--loading {
			/* loading class */
		}

		.uap .item-task-selector__search {
			width: 100%;
		}

			.uap .item-task-selector__search-input {
				width: 100%;
				padding: 10px 15px;
				color: #444;

				outline: none !important;

				line-height: 1;

				border: 1px solid #cdcdcd;

				border-bottom-left-radius: 0;
				border-bottom-right-radius: 0;

				transition: all 150ms ease-in-out;				
			}

				.uap .item-task-selector__search-input:hover {
					border-color: #b9b9b9;
				}

				.uap .item-task-selector__search-input:active,
				.uap .item-task-selector__search-input:focus {
					border-color: #0790e8;
				}

				.uap .item-task-selector__search-input,
				.uap .item-task-selector__search-input:hover,
				.uap .item-task-selector__search-input:active,
				.uap .item-task-selector__search-input:focus {
					box-shadow: 0 2px 5px 0 rgba(0,0,0,.1);
				}

		.uap .item-task-selector__options {
			border: 1px solid #cdcdcd;
			border-top: 0;
			border-bottom-right-radius: 8px;
			border-bottom-left-radius: 8px;

			max-height: calc(4em + 115px);
			overflow-y: auto;
		}

			.uap .item-task-selector__options::-webkit-scrollbar {
				width: 10px;
				background-color: rgba(0,0,0,.09);
				border-radius: 150px;
			}

			.uap .item-task-selector__options::-webkit-scrollbar-thumb:vertical {
				background: rgba(0,0,0,.3);
				border-radius: 150px;
				background-clip: padding-box;
				border: 2px solid rgba(0,0,0,0);
				min-height: 10px;
			}

			.uap .item-task-selector__option {
				padding: 10px 15px;
				color: #343434;
				cursor: pointer;
				position: relative;

				display: block;

				transition: all 150ms ease-in-out;
			}

				.uap .item-task-selector__option--show {}

				.uap .item-task-selector__option--hide {
					display: none;
				}

				.uap .item-task-selector__option--no-results {
					pointer-events: none;
					cursor: default;
				}

				.uap .item-task-selector__option:hover {
					background: #f3f3f3;
				}

				.uap .item-task-selector__option:not(:last-child){
					border-bottom: 1px solid #ececec;
				}

				.uap .item-task-selector__option-variable {
					color: #0790e8;
					font-weight: 500
				}

				.uap .item-task-selector-option__badge {
					display: inline-block;

					font-size: 13px;
					font-weight: 600;
					line-height: 1;

					color: #fff;
					background: #6bc45a;

					border-radius: 150px;

					padding: 3px 6px;

					margin-left: 3px;
				}

			.item-task-selector-options__group:not(:first-child) {
				border-top: 1px solid #cdcdcd;
			}

				.item-task-selector-options__group-header {
					background: #f3f3f3;
					color: #565555;
					padding: 10px 15px;
					border-top: 1px solid #cdcdcd;
					border-bottom: 1px solid #cdcdcd;
				}

					.item-task-selector-options__group-header .uo-icon {
						margin-right: 3px;
					}

				.uap .item-task-selector-options__group--available {}

					.uap .item-task-selector-options__group--available .item-task-selector-option__badge {
						background: #a4a4a4;
					}

				.item-task-selector-options__group--unavailable {}

					.item-task-selector-options__group--unavailable .item-task-selector__option {
						background: #f3f3f3;
						outline: 0 !important;
					}

					.item-task-selector-options__group--unavailable .item-task-selector__option-variable {
						color: #000;
					}

					.item-task-selector-options__group--unavailable .item-task-selector__option .uo-icon {
						margin-right: 3px;
					}

					.item-task-selector-options__group--unavailable:first-child {
						border-top: 0;
					}

			.uap .item__task-selector--loading .item-task-selector__option:not(.item-task-selector__option--loading) {
				opacity: .5;
				cursor: not-allowed;
			}

				.uap .item-task-selector__option--loading  {
					/* loading class */
					background: #f3f3f3;
				}

					.uap .item-task-selector__option--loading:after {
						content: '\e000';
						font-family: 'Uncanny Automator Icons';

						position: absolute;
						right: 15px;

						color: #666666;
						font-size: 16px;

						animation: rotating 2s linear infinite;
					}

	/* Options group */

	.uap .item-options__group {
		width: 100%;
		margin-bottom: 15px;
	}

	.uap .item-options__group:last-child {
		margin-bottom: 20px;
	}

		.uap .item-options__group--error {
			/* VALIDATION ERROR */
		}
